import _ from 'underscore';
import $ from 'jquery';

var url_regex =
  /^(?:([A-Za-z]+):)?(\/{0,3})([0-9.\-A-Za-z]+)(?::(\d+))?(?:\/([^?#]*))?(?:\?([^#]*))?(?:#(.*))?$/;

_.mixin({
  isURI: function(string) {
    return (url_regex.test(string));
  },

  titleize: function(str) {
    if (str === null) {
      return '';
    }
    str = String(str)
      .toLowerCase();
    return str.replace(/(?:^|\s|-)\S/g, function(c) {
      return c.toUpperCase();
    });
  },

  classify: function(str) {
    return _.titleize(String(str)
        .replace(/[\W_]/g, ' '))
      .replace(/\s/g, '');
  },

  classifyList: function(a) {
    return _.map(a, function(i) {
      return _.classify(i);
    });
  },

  capitalize: function(string) {
    return string.charAt(0)
      .toUpperCase() + string.substring(1)
      .toLowerCase();
  },

  underscored: function(str) {
    return _.trim(str)
      .replace(/([a-z\d])([A-Z]+)/g, '$1_$2')
      .replace(/[-\s]+/g, '_')
      .toLowerCase();
  },

  trim: function(string) {
    return string.replace(/^\s\s*/, '')
      .replace(/\s\s*$/, '');
  },

  reverse: function(str) {
    return str.split('')
      .reverse()
      .join('');
  },

  flattern: function(obj) {
    var x = {};
    _.each(obj, function(a, b) {
      x[(_.isArray(obj)) ? a : b] = true;
    });
    return x;
  },

  to_slug: function(str) {
    return str
      .toLowerCase()
      .replace(/[^\w ]+/g, '')
      .replace(/ +/g, '-');
  }
});

$.fn.visible = function() {
  return this.css('visibility', 'visible');
};

$.fn.invisible = function() {
  return this.css('visibility', 'hidden');
};

$.fn.visibilityToggle = function() {
  return this.css('visibility', function(i, visibility) {
    return (visibility == 'visible') ? 'hidden' : 'visible';
  });
};

$.fn.caretToEnd = function() {
  var range, selection;

  range = document.createRange();
  range.selectNodeContents(this[0]);
  range.collapse(false);

  selection = window.getSelection();
  selection.removeAllRanges();
  selection.addRange(range);

  return this;
};

// Extend jquery with flashing for elements
$.fn.flash = function(duration, iterations) {
  duration = duration || 1000; // Default to 1 second
  iterations = iterations || 2; // Default to 1 iteration
  let iterationDuration = Math.floor(duration / iterations);

  let originalColor = this.css('background-color');
  this.css('background-color', '#ffffdd');
  for (var i = 0; i < iterations; i++) {
    this.fadeOut(iterationDuration)
      .fadeIn(iterationDuration, function() {
        this.css('background-color', '#ffffff');
      });
  }
  this.css('background-color', originalColor);
  return this;
};

$.fn.shake = function(shakes, distance, duration) {
  shakes = shakes || 3;
  distance = distance || 10;
  duration = duration || 300;
  this.each(function() {
    $(this)
      .css('position', 'relative');
    for (var x = 1; x <= shakes; x++) {
      $(this)
        .animate({
          left: (distance * -1)
        }, (((duration / shakes) / 4)))
        .animate({
          left: distance
        }, ((duration / shakes) / 2))
        .animate({
          left: 0
        }, (((duration / shakes) / 4)));
    }
  });
  return this;
};

/***************************************************************************************************
LoadingOverlay - A flexible loading overlay jQuery plugin
    Author          : Gaspare Sganga
    Version         : 1.4.1
    License         : MIT
    Documentation   : http://gasparesganga.com/labs/jquery-loading-overlay/
****************************************************************************************************/
(function($, undefined){
    // Default Settings
    var _defaults = {
        color           : "rgba(255, 255, 255, 0.8)",
        custom          : "",
        fade            : true,
        fontawesome     : "",
        image           : "data:image/gif;base64,",
        imagePosition   : "center center",
        maxSize         : "100px",
        minSize         : "20px",
        resizeInterval  : 50,
        size            : "50%",
        zIndex          : undefined
    };

    $.LoadingOverlaySetup = function(settings){
        $.extend(true, _defaults, settings);
    };

    $.LoadingOverlay = function(action, options){
        switch (action.toLowerCase()) {
            case "show":
                var settings = $.extend(true, {}, _defaults, options);
                _Show("body", settings);
                break;

            case "hide":
                _Hide("body", options);
                break;
        }
    };

    $.fn.LoadingOverlay = function(action, options){
        switch (action.toLowerCase()) {
            case "show":
                var settings = $.extend(true, {}, _defaults, options);
                return this.each(function(){
                    _Show(this, settings);
                });

            case "hide":
                return this.each(function(){
                    _Hide(this, options);
                });
        }
    };


    function _Show(container, settings){
        container = $(container);
        var wholePage   = container.is("body");
        var count       = container.data("LoadingOverlayCount");
        if (count === undefined) count = 0;
        if (count == 0) {
            var overlay = $("<div>", {
                class   : "loadingoverlay",
                css     : {
                    "background-color"  : settings.color,
                    "display"           : "flex",
                    "flex-direction"    : "column",
                    "align-items"       : "center",
                    "justify-content"   : "center"
                }
            });
            if (settings.zIndex !== undefined) overlay.css("z-index", settings.zIndex);
            if (settings.image) overlay.css({
                "background-image"      : "url(" + settings.image + ")",
                "background-position"   : settings.imagePosition,
                "background-repeat"     : "no-repeat"
            });
            if (settings.fontawesome) $("<div>", {
                class   : "loadingoverlay_fontawesome " + settings.fontawesome
            }).appendTo(overlay);
            if (settings.custom) $(settings.custom).appendTo(overlay);
            if (wholePage) {
                overlay.css({
                    "position"  : "fixed",
                    "top"       : 0,
                    "left"      : 0,
                    "width"     : "100%",
                    "height"    : "100%"
                });
            } else {
                overlay.css("position", (container.css("position") == "fixed") ? "fixed" : "absolute");
            }
            _Resize(container, overlay, settings, wholePage);
            if (settings.resizeInterval > 0) {
                var resizeIntervalId = setInterval(function(){
                    _Resize(container, overlay, settings, wholePage);
                }, settings.resizeInterval);
                container.data("LoadingOverlayResizeIntervalId", resizeIntervalId);
            }
            if (!settings.fade) {
                settings.fade = [0, 0];
            } else if (settings.fade === true) {
                settings.fade = [400, 200];
            } else if (typeof settings.fade == "string" || typeof settings.fade == "number") {
                settings.fade = [settings.fade, settings.fade];
            }
            container.data({
                "LoadingOverlay"                : overlay,
                "LoadingOverlayFadeOutDuration" : settings.fade[1]
            });
            overlay
                .hide()
                .appendTo("body")
                .fadeIn(settings.fade[0]);
        }
        count++;
        container.data("LoadingOverlayCount", count);
    }

    function _Hide(container, force){
        container = $(container);
        var count = container.data("LoadingOverlayCount");
        if (count === undefined) return;
        count--;
        if (force || count <= 0) {
            var resizeIntervalId = container.data("LoadingOverlayResizeIntervalId");
            if (resizeIntervalId) clearInterval(resizeIntervalId);
            container.data("LoadingOverlay").fadeOut(container.data("LoadingOverlayFadeOutDuration"), function(){
                $(this).remove()
            });
            container.removeData(["LoadingOverlay", "LoadingOverlayCount", "LoadingOverlayFadeOutDuration", "LoadingOverlayResizeIntervalId"]);
        } else {
            container.data("LoadingOverlayCount", count);
        }
    }

    function _Resize(container, overlay, settings, wholePage){
        if (!wholePage) {
            var x = (container.css("position") == "fixed") ? container.position() : container.offset();
            overlay.css({
                top     : x.top + parseInt(container.css("border-top-width"), 10),
                left    : x.left + parseInt(container.css("border-left-width"), 10),
                width   : container.innerWidth(),
                height  : container.innerHeight()
            });
        }
        var c    = wholePage ? $(window) : container;
        var size = "auto";
        if (settings.size && settings.size != "auto") {
            size = Math.min(c.innerWidth(), c.innerHeight()) * parseFloat(settings.size) / 100;
            if (settings.maxSize && size > parseInt(settings.maxSize, 10)) size = parseInt(settings.maxSize, 10) + "px";
            if (settings.minSize && size < parseInt(settings.minSize, 10)) size = parseInt(settings.minSize, 10) + "px";
        }
        overlay.css("background-size", size);
        overlay.children(".loadingoverlay_fontawesome").css("font-size", size);
    }

}($));
